      {
        name: 'about',
        src: 'http://www.gppv-energy.com/Templates/cn/images/02.jpg',
        path: '/about?name=企业简介'
      }
    ]
    const handleClickImg = path => {
      router.push(path)
    }
    const handleOpen = (key, keyPath) => {
      console.log(key, keyPath)
    }
    const handleDownload = src => {
      window.open(src)
    }
    return {
      MENUS,
      route,
      handleClickImg,
      handleOpen,
      handleDownload
    }
  }
}
</script>

